body {
    background-color: #201f1f;
    color: #FFFFFF;
}

.container {
    max-width: 320px;
    margin: 0 auto;
}

.header {
    border-bottom: 1px solid #ccc;
}

.header__inner {
    text-align: center;
    padding: 20px 0;
}

.header__inner a {
    color: #FFFFFF;
    font-size: 28px;
    font-weight: bold;
}

.categories__list {
    margin-bottom: 30px;
}

.categories__list ul {
    padding: 10px 0;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    gap: 10px;
    overflow-y: hidden;
    overflow-x: auto;
}

.categories__list ul::-webkit-scrollbar {
    width: 10px;
    height: 3px;
    /* ширина scrollbar */
  }
  
.categories__list ul::-webkit-scrollbar-track {
    background: transparent;
    /* цвет дорожки */
}
  
.categories__list ul::-webkit-scrollbar-thumb {
    background-color: #FFFFFF;
    /* цвет плашки */
    border-radius: 4px;
    /* закругления плашки */
}
  

.categories__list ul li {
    display: flex;
    flex-direction: column;
    gap: 5px;
    text-align: center;
    max-width: 50px;
    cursor: pointer;
}

.categories__list ul li.active div {
    border-color: rgb(223, 86, 36);
}

.categories__list ul li div {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border: 2px solid #FFF;
}

.categories__list ul li span {
   font-size: 10px;
   line-height: 10px;
}

.products__list {
    padding-bottom: 50px;
}

.products__list ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  
}

.products__list ul li {
    width: 100%; 
}

.products__item {
    padding: 10px;
    border: 1px solid #FFF;
    border-radius: 20px;
    width: 100%;
}

.products__item img {
    height: 200px;
    width: 100%;
    object-fit: cover;
    margin-bottom: 15px;
    border-radius: 15px;
}

.products__item p {
    text-align: center;
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: bold;
}

.products__item-row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.products__item-col {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

.products__item-col span:nth-child(1) {
    color: #ccc;
    font-size: 12px;
}

.products__item-col span:nth-child(2) {
    font-weight: 500;
    font-size: 16px;
}